
a{
    text-decoration: none;
}
li{
    list-style: none;
    padding: 13px 0px;
    display: inline-block;
}
a {
    position: relative;
    color: #000;
    text-decoration: none;
}
.nav{
    margin-right: 30px;
}

a:visited {
    color: #000;
    text-decoration: none;
}

a:hover {
    color: #000;
    text-decoration: none;
}

.sidebarWrapper a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    bottom: -2px;
    left: 0;
    background-color: #000;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}
li.active a:before,
a:hover:before  {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

a.active{
    font-weight: bold;
    font-size: 19px;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    transition: all 0.1s

}
.friends{
    margin-left: 7px;
    font-weight: bold;
}